"use client";
import React, { useEffect, useState, useRef } from "react";
import { useJsApiLoader, GoogleMap, Marker } from "@react-google-maps/api";

const Page = () => {
    const [userPosition, setUserPosition] = useState<{ lat: number; lng: number } | null>(null);
    const defaultPosition = { lat: 33.878840, lng: -117.884973 }; // Default map center
    const [busStops, setBusStops] = useState<{ stop_id: string; stop_name: string; lat: number; lng: number }[]>([]);
    const [searchQuery, setSearchQuery] = useState("");
    const [routes, setRoutes] = useState<{ route_id: string; route_name: string; route_url: string }[]>([]);
    const [locationError, setLocationError] = useState<string | null>(null);
    const mapRef = useRef<google.maps.Map | null>(null);
    const markerRef = useRef<any>(null);
    const [mapCenter, setMapCenter] = useState(defaultPosition);

    const { isLoaded } = useJsApiLoader({
        googleMapsApiKey: process.env.NEXT_PUBLIC_GOOGLE_MAPS_API_KEY!,
        libraries: ["places", "maps"], 
    });

    // Separate function to handle location updates
    const handleLocationUpdate = (position: GeolocationPosition) => {
        const newPosition = {
            lat: Number(position.coords.latitude.toFixed(9)),
            lng: Number(position.coords.longitude.toFixed(9)),
        };
        setUserPosition(newPosition);
        setMapCenter(newPosition);
        fetchRoutes(newPosition.lat, newPosition.lng, setRoutes);
        setLocationError(null);
    };

    // Function to handle map center changes
    const handleMapCenterChanged = () => {
        if (mapRef.current) {
            const center = mapRef.current.getCenter();
            if (center) {
                const newCenter = {
                    lat: Number(center.lat().toFixed(9)),
                    lng: Number(center.lng().toFixed(9))
                };
                setMapCenter(newCenter);
                fetchRoutes(newCenter.lat, newCenter.lng, setRoutes);
            }
        }
    };

    // Function to update the advanced marker
    const updateUserMarker = () => {
        if (!mapRef.current || !userPosition || !window.google || !window.google.maps || !window.google.maps.marker) {
            console.log("Cannot create marker - missing dependencies:", {
                map: !!mapRef.current,
                userPosition: !!userPosition,
                google: !!window.google,
                maps: !!(window.google && window.google.maps),
                marker: !!(window.google && window.google.maps && window.google.maps.marker)
            });
            return;
        }
        
        try {
            // Remove existing marker if it exists
            if (markerRef.current) {
                markerRef.current.map = null;
            }
            
            // Create a new advanced marker
            const markerView = new window.google.maps.marker.AdvancedMarkerElement({
                map: mapRef.current,
                position: userPosition,
                title: "Your Location",
                content: createMarkerContent(),
            });
            
            markerRef.current = markerView;
            console.log("Advanced marker created successfully");
        } catch (error) {
            console.error("Error creating advanced marker:", error);
        }
    };

    // Function to create custom marker content
    const createMarkerContent = () => {
        const div = document.createElement('div');
        div.className = 'custom-marker';
        div.innerHTML = `
            <div style="
                background-color: #4285F4;
                border-radius: 50%;
                width: 20px;
                height: 20px;
                border: 2px solid white;
                box-shadow: 0 2px 6px rgba(0,0,0,0.3);
                display: flex;
                align-items: center;
                justify-content: center;
                color: white;
                font-weight: bold;
                font-size: 12px;
            ">
                You
            </div>
        `;
        return div;
    };

    // Update marker when user position changes
    useEffect(() => {
        if (isLoaded && userPosition) {
            // Add a small delay to ensure the marker library is fully loaded
            const timer = setTimeout(() => {
                updateUserMarker();
            }, 500);
            
            return () => clearTimeout(timer);
        }
    }, [isLoaded, userPosition]);

    // Fetch routes when map center changes
    useEffect(() => {
        if (isLoaded && mapCenter) {
            fetchRoutes(mapCenter.lat, mapCenter.lng, setRoutes);
        }
    }, [isLoaded, mapCenter]);

    useEffect(() => {
        // Immediately fetch routes with default position
        fetchRoutes(defaultPosition.lat, defaultPosition.lng, setRoutes);

        const fetchStopsData = async () => {
            try {
                const response = await fetch("/stops.csv");
                const stopsData = await response.text();

                const stops: { stop_id: string; stop_name: string; lat: number; lng: number }[] = [];
                const rows = stopsData.split("\n");

                rows.slice(1).forEach((row) => {
                    const columns = row.split(",");
                    if (columns.length === 4) {
                        const stop_id = columns[0].trim();
                        const stop_name = columns[1].trim();
                        const lat = parseFloat(columns[2].trim());
                        const lng = parseFloat(columns[3].trim());
                        if (!isNaN(lat) && !isNaN(lng)) {
                            stops.push({ stop_id, stop_name, lat, lng });
                        }
                    }
                });

                setBusStops(stops);
            } catch (error) {
                console.error("Error fetching stop data:", error);
            }
        };

        fetchStopsData();

        // Get initial position
        if (navigator.geolocation) {
            navigator.geolocation.getCurrentPosition(
                handleLocationUpdate,
                (error) => {
                    setLocationError(`Error getting location: ${error.message}`);
                    console.error("Error getting initial location:", error);
                },
                {
                    enableHighAccuracy: true,
                    maximumAge: 30000,
                    timeout: 27000,
                }
            );

            // Set up continuous watching with more relaxed parameters
            const watchId = navigator.geolocation.watchPosition(
                handleLocationUpdate,
                (error) => {
                    setLocationError(`Error tracking location: ${error.message}`);
                    console.error("Error watching location:", error);
                },
                {
                    enableHighAccuracy: true,
                    maximumAge: 60000, // Accept positions up to 1 minute old
                    timeout: 30000, // Wait up to 30 seconds for a position
                }
            );

            return () => {
                navigator.geolocation.clearWatch(watchId);
                // Clean up marker when component unmounts
                if (markerRef.current) {
                    markerRef.current.map = null;
                }
            };
        } else {
            setLocationError("Geolocation is not supported by your browser");
        }
    }, []);

    const fetchRoutes = async (lat: number, lng: number, setRoutes: (routes: any[]) => void) => {
        try {
            // Format coordinates to 9 decimal places
            const formattedLat = Number(lat.toFixed(9));
            const formattedLng = Number(lng.toFixed(9));

            // Log the request for debugging
            console.log(`Fetching routes for lat: ${formattedLat}, lon: ${formattedLng}`);
            
            // Use our new API route
            const apiUrl = `/api/routes?lat=${formattedLat}&lon=${formattedLng}`;
            console.log("API URL:", apiUrl);
            
            const response = await fetch(apiUrl);
            
            // Log the response status
            console.log(`API response status: ${response.status}`);
            
            if (!response.ok) {
                const errorText = await response.text();
                console.error(`API error: ${response.status} - ${errorText}`);
                // Don't use fallback data, just set empty routes
                setRoutes([]);
                return;
            }

            const data = await response.json();
            
            // Print the entire JSON response
            console.log("FULL JSON RESPONSE:", JSON.stringify(data, null, 2));
            
            // Also log it in a more readable format
            console.log("Routes data:", data);

            if (data.routes && Array.isArray(data.routes) && data.routes.length > 0) {
                console.log(`Found ${data.routes.length} routes`);
                
                const processedRoutes = data.routes.map((route: any) => {
                    const processedRoute = {
                        route_id: route.route_id || route.route_short_name || '',
                        route_name: route.route_desc || route.route_long_name || '',
                        route_url: route.route_url || '#',
                    };
                    console.log("Processed route:", processedRoute);
                    return processedRoute;
                });
                
                console.log("Setting routes:", processedRoutes);
                setRoutes(processedRoutes);
            } else {
                console.warn("No routes found in the response");
                // Don't use fallback data, just set empty routes
                setRoutes([]);
            }
        } catch (error) {
            console.error("Error fetching routes:", error);
            // Don't use fallback data, just set empty routes
            setRoutes([]);
        }
    };

    if (!isLoaded) {
        return <div>Loading...</div>;
    }

    return (
        <div style={{ height: "100vh", width: "100%", background: "linear-gradient(to bottom right, #1e3c72, #2a5298)", color: "#fff", display: "flex", flexDirection: "column" }}>
  {/* Top Header */}
  <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center", padding: "20px" }}>
    <h1 style={{ fontSize: "48px", fontWeight: "bold" }}>REROUTE</h1>
    <button style={{ fontSize: "18px", background: "none", border: "none", color: "#fff", cursor: "pointer" }}>FAQ</button>
  </div>

  {/* Main Content */}
  <div style={{ display: "flex", flex: 1, padding: "20px" }}>
    {/* Left Side: Search and Map */}
    <div style={{ flex: 1, marginRight: "20px", display: "flex", flexDirection: "column" }}>
      {/* Search Bar */}
      <input 
        type="text" 
        placeholder="Location Search" 
        value={searchQuery} 
        onChange={(e) => setSearchQuery(e.target.value)} 
        style={{
          padding: "12px",
          borderRadius: "8px",
          border: "none",
          fontSize: "16px",
          marginBottom: "20px",
          backgroundColor: "#eee",
          color: "#333"
        }} 
      />

      {/* Map */}
      <div style={{ flex: 1, borderRadius: "15px", overflow: "hidden" }}>
        <GoogleMap
          zoom={14}
          center={userPosition || defaultPosition}
          mapContainerStyle={{ height: "100%", width: "100%" }}
          options={{
            fullscreenControl: false,
            streetViewControl: false,
            mapTypeControl: false,
            zoomControl: true,
            gestureHandling: "greedy",
            styles: [
              {
                "elementType": "geometry",
                "stylers": [{ "color": "#242f3e" }]
              },
              {
                "elementType": "labels.text.stroke",
                "stylers": [{ "lightness": -80 }]
              },
              {
                "featureType": "administrative.locality",
                "elementType": "labels.text.fill",
                "stylers": [{ "color": "#746855" }]
              },
              {
                "featureType": "poi",
                "elementType": "labels.text.fill",
                "stylers": [{ "color": "#d59563" }]
              },
              {
                "featureType": "poi.park",
                "elementType": "geometry",
                "stylers": [{ "color": "#263c3f" }]
              },
              {
                "featureType": "poi.park",
                "elementType": "labels.text.fill",
                "stylers": [{ "color": "#6b9a76" }]
              },
              {
                "featureType": "road",
                "elementType": "geometry",
                "stylers": [{ "color": "#38414e" }]
              },
              {
                "featureType": "road",
                "elementType": "geometry.stroke",
                "stylers": [{ "color": "#212a37" }]
              },
              {
                "featureType": "road",
                "elementType": "labels.text.fill",
                "stylers": [{ "color": "#9ca5b3" }]
              },
              {
                "featureType": "road.highway",
                "elementType": "geometry",
                "stylers": [{ "color": "#746855" }]
              },
              {
                "featureType": "road.highway",
                "elementType": "geometry.stroke",
                "stylers": [{ "color": "#1f2835" }]
              },
              {
                "featureType": "road.highway",
                "elementType": "labels.text.fill",
                "stylers": [{ "color": "#f3d19c" }]
              },
              {
                "featureType": "transit",
                "elementType": "geometry",
                "stylers": [{ "color": "#2f3948" }]
              },
              {
                "featureType": "transit.station",
                "elementType": "labels.text.fill",
                "stylers": [{ "color": "#d59563" }]
              },
              {
                "featureType": "water",
                "elementType": "geometry",
                "stylers": [{ "color": "#17263c" }]
              },
              {
                "featureType": "water",
                "elementType": "labels.text.fill",
                "stylers": [{ "color": "#515c6d" }]
              },
              {
                "featureType": "water",
                "elementType": "labels.text.stroke",
                "stylers": [{ "lightness": -20 }]
              }
            ],
          }}
          onLoad={(map) => {
            mapRef.current = map;
            if (userPosition) {
              // Add a small delay to ensure the marker library is fully loaded
              setTimeout(() => {
                updateUserMarker();
              }, 500);
            }
          }}
          onDragEnd={handleMapCenterChanged}
          onZoomChanged={handleMapCenterChanged}
        >
          {/* Advanced markers are now managed via the updateUserMarker function */}
        </GoogleMap>
      </div>
    </div>

    {/* Right Side: Routes List */}
    <div style={{ flex: 1, background: "#1e3c72", borderRadius: "15px", padding: "20px", display: "flex", flexDirection: "column" }}>
      <h2 style={{ fontSize: "24px", marginBottom: "20px" }}>COMMON TRANSIT ({routes.length} routes)</h2>
      <div style={{ flex: 1, overflowY: "auto" }}>
        {routes.length === 0 ? (
          <p>No nearby routes found. {locationError ? `Error: ${locationError}` : ''}</p>
        ) : (
          routes.map((route) => (
            <div key={route.route_id} style={{ background: "linear-gradient(to right, #1e3c72, #2a5298)", padding: "15px", borderRadius: "10px", marginBottom: "10px", display: "flex", alignItems: "center", justifyContent: "space-between" }}>
              <div>
                <div style={{ fontSize: "18px", fontWeight: "bold" }}>OCTA BUS {route.route_id}</div>
                <div style={{ fontSize: "14px", marginTop: "5px" }}>{route.route_name}</div>
                <div style={{ fontSize: "12px", marginTop: "5px" }}>10 Minutes Arrival Prediction: 13 Minutes</div>
              </div>
              <div style={{ fontSize: "24px" }}>➔</div>
            </div>
          ))
        )}
      </div>
    </div>
  </div>
</div>
    );
};

export default Page;
